<template>
  <div class="deblur-gan" v-loading="!model">
    <el-row>
      <div class="image-block">
        <img
          id="deblur-gan-input"
          ref="deblurGANInput"
          width="360"
          height="360"
        />
      </div>
      <div class="image-block" v-loading="processing">
        <img
          id="deblur-gan-result"
          ref="deblurGANResult"
          width="360"
          height="360"
        />
      </div>
    </el-row>
    <el-row>
      <el-button @click="$refs.uploader.click()">
        打开图片
      </el-button>
      <input ref="uploader" type="file" @change="handleFileChange" hidden />
    </el-row>
    <el-row>
      <img ref="sample" src="static/sample1.png" width="64" height="64" @click="handleSampleClick">
    </el-row>
    <el-row>
      <a href="https://arxiv.org/abs/1711.07064" target="_blank">
        DeblurGAN: Blind Motion Deblurring Using Conditional Adversarial Networks
      </a>
    </el-row>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';

export default {
  name: 'DeblurGAN',
  data() {
    return {
      model: null,
      processing: false,
    };
  },

  methods: {
    handleFileChange() {
      if (!this.$refs.uploader.files.length) {
        return;
      }

      const file = this.$refs.uploader.files[0];
      const imgUrl = URL.createObjectURL(file);
      const img = new Image();
    
      img.onload = () => {
        img.width = Math.ceil((img.width - 32) / 224) * 224 + 32;
        img.height = Math.ceil((img.height - 32) / 224) * 224 + 32;
        this.makePrediction(img);
      };

      img.src = imgUrl;
      this.$refs.deblurGANInput.src = imgUrl;
    },

    handleSampleClick() {
      const img = new Image();
    
      img.onload = () => {
        img.width = Math.ceil((img.width - 32) / 224) * 224 + 32;
        img.height = Math.ceil((img.height - 32) / 224) * 224 + 32;
        this.makePrediction(img);
      };

      img.src = this.$refs.sample.src;
      this.$refs.deblurGANInput.src = this.$refs.sample.src;
    },

    async makePrediction(data) {
      const makeBatchPrediction = async () => {
        if (iterateCount >= preprocessed.length) {
          const merged = tf.tidy(() => {
            const predictionsTensor = predictions.map(prediction => {
              const predictionTensor = tf.tensor(prediction, [256, 256, 3]).slice([16, 16], [224, 224]);
              const deprocess = tf.clipByValue(tf.add(predictionTensor, 0.5), 0, 1);
              return deprocess;
            });
            const mergedX = [];
            for (let i = 0; i < blockSizeX; i += 1) {
              mergedX[i] = tf.concat(predictionsTensor.slice(i * blockSizeY, (i + 1) * blockSizeY), 1);
            }
            return tf.concat(mergedX);
          });

          var canvasOutput = document.createElement("canvas");
          await tf.toPixels(merged, canvasOutput);
          canvasOutput.toBlob((blob) => {
            this.$refs.deblurGANResult.src = URL.createObjectURL(blob);
            this.processing = false;
          });
          merged.dispose();
          return;
        }

        this.processing = true;

        const frame = tf.tidy(() => {
          return this.model.predict(tf.tensor(preprocessed[iterateCount], [1, 256, 256, 3]))
        });

        // Free memory
        predictions[iterateCount] = await frame.data();
        frame.dispose();

        iterateCount += 1;
        await tf.nextFrame();
        setTimeout(makeBatchPrediction, 0);
      }

      const blockSizeX = Math.ceil((data.height - 32) / 224);
      const blockSizeY = Math.ceil((data.width - 32) / 224);

      const preprocessed = await tf.tidy(() => {
        const imgTensor = tf.fromPixels(data).asType('float32');
        const preprocessed = tf.sub(tf.div(imgTensor, 255.), 0.5);
        const imgBlocks = [];
        for (let i = 0; i < blockSizeX; i += 1) {
          for (let j = 0; j < blockSizeY; j += 1) {
            imgBlocks.push(preprocessed.slice([i * 224, j * 224], [256, 256]).data());
          }
        }
        return Promise.all(imgBlocks);
      });

      let iterateCount = 0;
      const predictions = [];
      
      await tf.nextFrame();
      makeBatchPrediction();
    },
  },

  async mounted() {
    this.model = await tf.loadModel('models/deblur_gan/model.json');
  },
}
</script>

<style>
.deblur-gan .el-row {
  margin-bottom: 16px;
}

.deblur-gan .image-block {
  display: inline-block;
}
</style>
